.mainMovie {
    display: flex;
    justify-content: center;
    text-align: center;
    flex-direction: column;
    margin: 3rem auto;
    color: whitesmoke;
}

.poster {
    width: 12rem;
}
.table {
    display: flex;
    text-align: left;
    margin: 1rem;
}
.wrapSection {
    margin: 1rem;
}
.righteBar {
    margin: 1rem;
}
.homepageBtn {
    text-decoration: none;
    font-size: 1rem;
    color: orange;
}

@media (min-width: 1200px) {
    .mainMovie {
        display: flex;
        flex-direction: column;
        margin: 3rem auto;
        color: whitesmoke;
    }

    .poster {
        width: 300px;
    }

    .wrapSection {
        display: flex;
        flex-direction: row;
        margin: 1rem;
    }
    .righteBar {
        margin: 2rem;
    }
}
