.wrapHeader {
    position: fixed;
    width: 100%;
    display: flex;
    justify-content: center;
    flex-direction: column;
    background: linear-gradient(
        180deg,
        rgba(2, 0, 36, 1) 50%,
        rgba(255, 115, 0, 0.85) 100%
    );
    height: 4rem;
    padding: 0.2rem;
    z-index: 10000;
}
.wrapNav {
    display: flex;
    list-style-type: none;
    justify-content: flex-start;
    padding: 0;
    width: 50%;
}
.HeaderMenu > a {
    text-decoration: none;
    color: white;
    font-family: 'PF DinDisplay Medium', 'Trebuchet MS', Arial;
    font-size: 1.1rem;
    margin-left: 1rem;
    padding-bottom: 1rem;
}
.HeaderMenuHome {
    margin-bottom: 1rem;
}
.HeaderMenuHome > a {
    text-decoration: none;
    color: white;
    font-family: 'PF DinDisplay Medium', 'Trebuchet MS', Arial;
    font-size: 1.1rem;
    margin-left: 1rem;
}
.HeaderMenuHome:hover,
.HeaderMenu:hover {
    text-decoration: none;
    text-shadow: 0 1px 0 rgb(255 255 255 / 10%),
        0 0 10px rgb(255 255 255 / 100%);
}
.hiddenHeaderMenu {
    display: none;
    flex-direction: column;
    justify-content: center;
    text-align: center;
    background-color: rgba(2, 0, 36, 1);
    border-radius: 5%;
    width: 7rem;
    height: 7rem;
    position: fixed;
}

.HeaderMenu:hover ~ .hiddenHeaderMenu {
    display: flex;
    opacity: 1;
    transition: 0.5s;
    transform: translate3d(0, 5px, 0);
}
.hiddenHeaderMenu:hover {
    display: flex;
    transition: 0.5s;
    transform: translate3d(0, 5px, 0);
}
.hiddenHeaderMenu > a {
    text-decoration: none;
    color: white;
    font-family: 'PF DinDisplay Medium', 'Trebuchet MS', Arial;
    font-size: 1rem;
    padding: 0.5rem;
}

.hiddenHeaderMenu > a:hover {
    color: #2f364a;
    text-shadow: 0 1px 0 rgb(255 255 255 / 10%),
        0 0 10px rgb(255 255 255 / 100%);
}

.menuIsActiv {
    text-shadow: 0 1px 0 rgb(255 255 255 / 10%),
        0 0 10px rgb(255 255 255 / 100%);
}

/* Large desktops and laptops */
@media (min-width: 1200px) {
    .wrapHeader {
        position: fixed;
        display: flex;
        justify-content: center;
        flex-direction: row;
        width: 1200px;
        background: linear-gradient(
            180deg,
            rgba(2, 0, 36, 1) 50%,
            rgba(255, 115, 0, 0.85) 100%
        );
        height: 50px;
        padding: 0;
        z-index: 10000;
    }
    .wrapNav {
        display: flex;
        list-style-type: none;
        justify-content: flex-start;
        padding: 0;
        width: 50%;
    }
    .HeaderMenu > a {
        text-decoration: none;
        color: white;
        font-family: 'PF DinDisplay Medium', 'Trebuchet MS', Arial;
        font-size: 1.1rem;
        margin-left: 1rem;
        padding-bottom: 1rem;
    }
    .HeaderMenuHome > a {
        text-decoration: none;
        color: white;
        font-family: 'PF DinDisplay Medium', 'Trebuchet MS', Arial;
        font-size: 1.1rem;
        margin-left: 1rem;
    }
    .HeaderMenuHome:hover,
    .HeaderMenu:hover {
        text-decoration: none;
        text-shadow: 0 1px 0 rgb(255 255 255 / 10%),
            0 0 10px rgb(255 255 255 / 100%);
        top: 5rem;
    }
    .hiddenHeaderMenu {
        display: none;
        flex-direction: column;
        justify-content: center;
        text-align: center;
        background-color: rgba(2, 0, 36, 1) 50%;
        border-radius: 5%;
        width: 7rem;
        height: 7rem;
        position: relative;
        top: 1.5rem;
        right: 6rem;
    }

    .HeaderMenu:hover ~ .hiddenHeaderMenu {
        display: flex;
        opacity: 1;
        transition: 0.5s;
        transform: translate3d(0, 5px, 0);
        z-index: 10000;
    }
    .hiddenHeaderMenu:hover {
        display: flex;
        transition: 0.5s;
        transform: translate3d(0, 5px, 0);
        top: 1.5rem;
        z-index: 10000;
    }
    .hiddenHeaderMenu > a {
        text-decoration: none;
        color: white;
        font-family: 'PF DinDisplay Medium', 'Trebuchet MS', Arial;
        font-size: 1rem;
        padding: 0.5rem;
    }

    .hiddenHeaderMenu > a:hover {
        color: #2f364a;
        text-shadow: 0 1px 0 rgb(255 255 255 / 10%),
            0 0 10px rgb(255 255 255 / 100%);
    }
    .menuIsActiv {
        text-shadow: 0 1px 0 rgb(255 255 255 / 10%),
            0 0 10px rgb(255 255 255 / 100%);
    }
}

/* Landscape tablets and medium desktops */
@media (min-width: 992px) and (max-width: 1199px) {
    .wrapHeader {
        position: fixed;
        width: 100%;
        display: flex;
        justify-content: center;
        flex-direction: row;
        background-color: #2f364a;
        height: 3rem;
        padding: 0.2rem;
        z-index: 10000;
        border-bottom: 1px solid black;
    }
    .wrapNav {
        display: flex;
        list-style-type: none;
        justify-content: flex-start;
        padding: 0;
        width: 50%;
    }
    .hiddenHeaderMenu {
        display: none;
        flex-direction: column;
        justify-content: center;
        text-align: center;
        background-color: rgba(2, 0, 36, 1);
        border-radius: 5%;
        width: 7rem;
        height: 7rem;
        position: relative;
        top: 1.5rem;
        right: 6rem;
    }
}

/* Portrait tablets and small desktops */
@media (min-width: 768px) and (max-width: 991px) {
    .wrapHeader {
        position: fixed;
        width: 100%;
        display: flex;
        justify-content: center;
        flex-direction: row;
        background-color: #2f364a;
        height: 3rem;
    }
    .wrapNav {
        display: flex;
        list-style-type: none;
        justify-content: flex-start;
        padding: 0;
        width: 50%;
    }
    .hiddenHeaderMenu {
        display: none;
        flex-direction: column;
        justify-content: center;
        text-align: center;
        background-color: rgba(2, 0, 36, 1);
        border-radius: 5%;
        width: 7rem;
        height: 7rem;
        position: relative;
        top: 1.5rem;
        right: 6rem;
    }
}

