.logoIMG {
    display: flex;
    justify-content: center;
    align-items: center;
    transition: all 0.2s linear;
    font-size: 1rem;
    text-transform: uppercase;
    font-weight: 300;
    font-family: 'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;
    text-decoration: none;
    color: white;
    margin-bottom: 0.5rem;
}
.logoIMG:hover {
    text-decoration: none;
    text-shadow: 0 1px 0 rgb(255 255 255 / 10%), 0 0 15px rgb(255 255 255 / 74%);
}
.TV {
    color: orange;
    font-weight: 900;
}

@media (min-width: 1200px) {
    .logoIMG {
        display: flex;
        justify-content: center;
        align-items: center;
        transition: all 0.2s linear;
        font-size: 3rem;
        text-transform: uppercase;
        font-weight: 300;
        font-family: 'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;
        text-decoration: none;
        color: white;
        margin: 0;
    }
}
@media (min-width: 992px) and (max-width: 1199px) {
    .logoIMG {
        margin: 0;
        font-size: 2rem;
    }
}
@media (min-width: 768px) and (max-width: 991px) {
    .logoIMG {
        margin: 0;
        font-size: 2rem;
    }
}
