.containerSlider {
    max-width: 1100px;
    margin: 50px auto;
    padding-top: 1rem;
    margin-bottom: 0;
}

.containerSlider img {
    width: 270px;
    border-radius: 2%;
}
.containerSlider img:hover {
    box-shadow: 0 1px 4px 0 rgba(0, 0, 0, 0.7);
    cursor: pointer;
}

.containerSlider button {
    display: none !important;
}

.mainTrending {
    display: flex;
    justify-content: center;
    flex-direction: column;
    max-width: 1200px;
    margin: 0 auto;
    height: 100%;
}

.itemsWrap {
    padding: 0;
    margin: 0.5rem auto;
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
}

.container {
    display: flex;
    border-radius: 2%;
    margin: 1%;
    box-shadow: 0 1px 4px 0 rgba(0, 0, 0, 0.7);
}

.containerSVG {
    position: relative;
    background-size: cover;
    display: flex;
    justify-content: center;
    background-color: transparent;
}

.mainIMG {
    height: 100%;
    max-width: 200px;
    margin: 0;
    border-radius: 2%;
    object-fit: cover;
}

.title {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    height: 100%;
    font-family: 'PF DinDisplay Medium', 'Trebuchet MS', Arial;
    font-weight: 600;
    font-size: 0.5rem;
    position: absolute;
    opacity: 0;
    text-align: center;
}

.imgSVGPlayBtn {
    fill: rgba(255, 115, 0, 0.85);
}

.btnForSvg {
    position: absolute;
    color: black;
    top: 5%;
    left: 5%;
    z-index: 3;
    transition: 0.5s;
    width: 2rem;
    height: 2rem;
    border-radius: 10px;
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    border: none;
    outline: none;
    opacity: 0;
}

.contForSvgStar {
    position: absolute;
    color: black;
    top: 5%;
    left: 70%;
    z-index: 3;
    transition: 0.5s;
    width: 3rem;
    height: 2rem;
    border-radius: 10px;
    display: flex;
    flex-direction: row;
    justify-content: space-around;
    align-items: center;
    border: none;
    outline: none;
    opacity: 0;
}

.imgSVG {
    fill: currentColor;
    width: 1em;
    height: 1em;
    display: inline-block;
    font-size: 1.5rem;
    transition: fill 200ms cubic-bezier(0.4, 0, 0.2, 1) 0ms;
    flex-shrink: 0;
    user-select: none;
}
.imgSVGStar {
    fill: currentColor;
    width: 0.7em;
    height: 0.7em;
    display: inline-block;
    font-size: 1.5rem;
    transition: fill 200ms cubic-bezier(0.4, 0, 0.2, 1) 0ms;
    flex-shrink: 0;
    user-select: none;
    color: gold;
    padding: 5%;
}
.voteAverage {
    color: gold;
    padding: 5%;
}

.container:hover .btnForSvg {
    transform: scale(1.03);
    transition: 0.5s linear;
    opacity: 1;
    cursor: pointer;
    background-color: rgba(185, 185, 182, 0.596);
}

.container:hover .contForSvgStar {
    opacity: 1;
    background-color: rgba(185, 185, 182, 0.596);
}

.container:hover .title {
    opacity: 1;
    transition: 0.5s linear;
    z-index: 2;
}
.container:hover .mainIMG,
.imgSVG:hover {
    filter: brightness(30%);
}
.imgSVG:hover {
    color: gold;
    filter: brightness(100%);
}
.dNone {
    display: none;
}

@media (max-width: 1200px) {
    .containerSlider {
        display: none;
    }
}
@media (min-width: 1200px) {
    .wrapQueueWathcedMain {
        margin: 3rem auto;
        max-width: 1200px;
        background: linear-gradient(
            180deg,
            rgba(2, 0, 36, 1) 30%,
            rgba(47, 54, 74, 0.6530987394957983) 100%
        );
    }
}
@media (min-width: 992px) and (max-width: 1199px) {
    .itemsWrap {
        margin: 4.5rem auto;
    }
}
@media (min-width: 768px) and (max-width: 991px) {
    .itemsWrap {
        margin: 4.5rem auto;
    }
}
@media (max-width: 767px) {
    .itemsWrap {
        margin: 4.5rem auto;
    }
}
